.menu_link {
  opacity: .4;
  font-family: 'Futura-Medium';
  font-size: 16px;
  line-height: 16px;
  color: #ffffff;
  letter-spacing: 2.29px;
  text-align: left;
  border-bottom: 0;
}

.menu_link:hover {
  opacity: 1;
  color: #ffffff;
}

.menu_link-arrow {
  position: relative;
  padding-right: 14px;
}

.menu_link-arrow:after {
  position: absolute;
  content: '';
  right: 0;
  top: calc( 16px - ( 7px / 2 ));
  transform: translateY(-50%);
  width: 11px;
  height: 7px;
  background-image: svg-load("arrow-bottom.svg");
}

