// адаптивный видео-блок
.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
  border: 1px solid;

  & iframe,
  & object,
  & embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

// Полпробела
span.tsp {
  display: inline-block;
  width: .12em;
  overflow: hidden;
  text-decoration: inherit;
}

hr {
  margin-top:    $line-height-computed;
  margin-bottom: $line-height-computed;
  border: 0;
  border-top: 1px solid $hr-border;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: $headings-font-family;
  font-weight: $headings-font-weight;
  line-height: $headings-line-height;
  color: $headings-color;

  small,
  .small {
    font-weight: normal;
    line-height: 1;
    color: #777;
  }
}

h1, .h1,
h2, .h2,
h3, .h3 {
  margin-top: $line-height-computed;
  margin-bottom: ($line-height-computed / 2);

  small, .small { font-size: 65%; }
}

h4, .h4,
h5, .h5,
h6, .h6 {
  margin-top: ($line-height-computed / 2);
  margin-bottom: ($line-height-computed / 2);

  small, .small { font-size: 75%; }
}

h1, .h1 { font-size: $font-size-h1; }
h2, .h2 { font-size: $font-size-h2; }
h3, .h3 { font-size: $font-size-h3; }
h4, .h4 { font-size: $font-size-h4; }
h5, .h5 { font-size: $font-size-h5; }
h6, .h6 { font-size: $font-size-h6; }

p {
  margin: 0 0 ($line-height-computed / 2);
}

// Отступы
p, ul, ol, pre, table, blockquote {
  margin-top: 0px;
  margin-bottom: 21px;
}

ul ul, ol ol, ul ol, ol ul {
  margin-top: 0px;
  margin-bottom: 0px;
}

code {
  padding: 2px 4px;
  font-size: 90%;
  color: $code-color;
  background-color: $code-bg;
  border-radius: $border-radius-base;
}
.text-pink { color: $pink-color; }

.headline {
  position: relative;
  max-width: 630px;
  text-align: left;

  &:before {
    position: absolute;
    content: '';
    opacity: .1;
    font-family: 'Futura-Medium';
    font-size: 156px;
    color: #3A3F54;
    letter-spacing: 0;
    line-height: 90px;
    top: -75px;
    left: -160px;
    z-index: 0;
  }

  &_title {
    position: relative;
    z-index: 1;
  }

  &_description {
    margin-top: 20px;
    margin-bottom: 0;
    font-family: '.SFNSDisplay';
    font-size: 22px;
    color: #1C2030;
    letter-spacing: 0;
    line-height: 35px;

    p:last-child { margin-bottom: 0; }
  }

  &.headline-dark {
    color: #ffffff;

    &:before {
      opacity: 1;
      color: #000000;
    }

    .headline_description {
      color: #ffffff;
    }
  }
}
